<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="time">
          <el-date-picker
            v-model="query.time"
            type="daterange"
            align="right"
            unlink-panels
            value-format="yyyy-MM-dd"
            range-separator="至"
            start-placeholder="任务发起日期"
            end-placeholder="任务审批日期"
            style="width: 260px;"
            :picker-options="pickerOptions"
          />
        </el-form-item>
        <el-form-item prop="formName">
          <el-input v-model="query.formName" placeholder="处理业务名称搜索" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-button class="filter-item" type="success" icon="el-icon-search" @click="init">搜索</el-button>
        <el-button class="filter-item" type="warning" icon="el-icon-refresh-left" @click="resetForm('filterItem')">重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        index
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-view" @click="handleModify(scope.row)" />
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { geMyAppliedTaskApi as getListApi } from '@/apis/modules/common/task'
import moment from 'moment'
moment.locale('zh-cn')

export default {
  name: 'SaleDashboardMessage',
  components: {},
  mixins: [paginationPage],
  data() {
    return {
      loading: false,
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      query: {
        time: ['', ''],
        formName: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'formName',
          label: '处理业务',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'taskName',
          label: '任务名称',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'starter',
          label: '发起人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'officeName',
          label: '发起人部门',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'startTime',
          label: '发起时间',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (c) => moment(c.startTime).format('YYYY-MM-DD HH:mm:ss'),
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'approver',
          label: '待办',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'endTime',
          label: '处理时间',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (c) => moment(c.endTime).format('YYYY-MM-DD HH:mm:ss'),
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'duration_show',
          label: '累计用时',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        const { time: [startTime, endTime], formName } = this.query
        getListApi({
          page: {
            ...this.pageInfo
          },
          startTime, endTime,
          formName
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(item => {
              const { duration, startTime, endTime, ...info } = item
              let duration_show = ''
              if (startTime && endTime) {
                duration_show = moment(startTime).from(endTime, true)
              }
              return {
                duration_show,
                duration,
                startTime, endTime,
                ...info
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    // 跳转到审核页面
    handleModify(item) {
      const { businessKey, taskId, processInstanceId, taskKey } = item
      const id = businessKey.split(':')[1]
      const billNumber = businessKey.split(':')[0] // 单据编号
      if (!id) return
      this.$router.push({
        name: taskKey,
        params: {
          id
        },
        query: {
          billNumber,
          taskId,
          processInstanceId,
          type: 'auditHistory'
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.init()
    }
  }
}
</script>

